<template>
  <ec-modal
    :visible="visible"
    :title="title"
    size="large"
    height="450px"
    class="ec-draw-form"
    @cancel="onclose"
    :is-confirm="true"
  >
    <ec-form :model="formData" :rules="rules" ref="modelRef">
      <a-row type="flex" :gutter="12">
        <a-col :span="12">
          <a-form-model-item label="申请单位" prop="applyOrgCode" ref="applyOrgCode">
            <org-picker :disabled="true" v-model="formData.applyOrgCode" placeholder="请选择" />
          </a-form-model-item>
        </a-col>

        <a-col :span="12">
          <a-form-model-item ref="leafTypeCode" prop="leafTypeCode" label="烟叶类型">
            <ec-select placeholder="请选择" v-model="formData.leafTypeCode" @select="handleSpec">
              <a-select-option v-for="leafType in leafTypeList" :key="leafType.leafTypeCode">
                {{ leafType.leafTypeName }}
              </a-select-option>
            </ec-select>
          </a-form-model-item>
        </a-col>

        <a-col :span="12">
          <a-form-model-item ref="packType" prop="packType" label="包装类型">
            <ec-select placeholder="请选择" v-model="formData.packType" @select="handleSpec">
              <a-select-option v-for="packType in packTypeList" :key="packType.enumKey">
                {{ packType.name }}
              </a-select-option>
            </ec-select>
          </a-form-model-item>
        </a-col>
        <a-col :span="12">
          <a-form-model-item ref="spec" prop="spec" label="规格（KG/件）">
            <ec-select
              placeholder="请选择"
              v-model="formData.spec"
              @select="handleApplyNum('spec')"
            >
              <a-select-option v-for="rtSpec in specList" :key="rtSpec.spec">
                {{ rtSpec.spec }}
              </a-select-option>
            </ec-select>

            <!-- <a-select v-model="formData.spec" placeholder="请选择" @select="handleApplyNum">
              <a-select-option
                v-for="(item, index) in leafTypeList"
                :key="index"
                :value="item.leafTypeCode"
                >{{ item.leafTypeName }}
              </a-select-option>
            </a-select> -->
          </a-form-model-item>
        </a-col>

        <a-col :span="12">
          <a-form-model-item :label="applyLable" prop="applyWeight">
            <a-input-number
              :min="0"
              :max="100000"
              :precision="2"
              v-model="formData.applyWeight"
              @change="handleApplyNum('applyWeight')"
            />
          </a-form-model-item>
        </a-col>
        <a-col :span="12">
          <a-form-model-item label="申请码包数量" prop="applyNum">
            <a-input-number :disabled="true" :min="0" v-model="formData.applyNum" />
          </a-form-model-item>
        </a-col>
      </a-row>
    </ec-form>
    <template v-slot:footer>
      <a-button type="primary" @click="onSave" :loading="saving">保存</a-button>
      <a-button @click="onclose">取消</a-button>
    </template>
  </ec-modal>
</template>

<script>
import _ from 'lodash';
import { OrgPicker } from '@euler-material/ecm-ui';

export default {
  name: 'batchManageForm',
  components: {
    OrgPicker,
  },
  data() {
    const orgNameValid = (rule, value, callback) => {
      if (value.length === 0) {
        return callback(new Error('请选择供方单位'));
      }

      callback();
    };
    return {
      visible: false,
      formData: {
        applyOrgCode: undefined,
        leafTypeCode: undefined,
        packType: undefined,
        spec: undefined,
        applyWeight: undefined,
        applyNum: undefined,
      },
      applyLable: '申请量（KG）',
      isEditor: false,
      saving: false,
      isDisableYear: true,
      busiYear: '',
      title: '',
      leafTypeList: [],
      packTypeList: [],
      specList: [],
      applyOrgCode: '',
      applyOrgName: '',
      oldFormData: {},
      weightUnitName: '',
      weightUnit: '',
      rules: {
        applyOrgCode: [{ validator: orgNameValid, required: true, trigger: 'change' }],
        leafTypeCode: { required: true, message: '请选择烟叶类型', trigger: 'blur' },
        packType: {
          required: true,
          message: '请输入包装类型',
          trigger: 'blur',
        },
        spec: {
          required: true,
          message: '请选择规格',
          trigger: 'blur',
        },
        applyWeight: {
          required: true,
          message: '请输入申请量',
          trigger: 'blur',
        },
        applyNum: {
          required: true,
          message: '请输入申请码包数量',
          trigger: 'blur',
        },
      },
    };
  },
  methods: {
    async show(data) {
      this.visible = true;
      const type = data.type || '';
      const applyOrgCode = data?.orgCodeList || '';
      // this.orgCodeList = orgCodeList;

      const selectedRow = data?.selectedRow || {};
      const enumMap = data?.enumMap || {};
      const applyStateList = enumMap?.PC_APPLY_STATE || [];
      this.applyStateList = applyStateList;

      this.leafTypeList = data?.oldLeafTypeList || [];

      const packTypeList = enumMap?.PC_PACK_TYPE || [];

      this.weightUnitName = data.weightUnitObj.weightUnitName;
      this.weightUnit = data.weightUnitObj.weightUnitCd;

      // this.formData = {};
      this.applyLable = `申请量（${this.weightUnitName}）`;
      this.packTypeList = packTypeList;
      if (type === 'editor') {
        this.isEditor = true;
        this.title = '编辑';
        this.oldFormData = _.cloneDeep(selectedRow);
        selectedRow.applyWeight = Number(selectedRow.applyWeight / this.weightUnit).toFixed(2);
        this.formData = selectedRow;
        this.formData.applyOrgCode = this.formData.applyOrgCode || applyOrgCode;

        await this.handleSpec();
      } else {
        this.isEditor = false;
        this.title = '新增';
        this.formData.tranYear = data.busiYear;
        this.formData.applyOrgName = data.orgName;
        this.formData.applyOrgCode = data.orgCode;
        this.formData.applyType = 'BU_CP'; // 写死
        this.formData.applyOrgCode = applyOrgCode;
        if (this.leafTypeList && this.leafTypeList?.length) {
          const leafTypeObj = this.leafTypeList.find((item) => item.leafTypeName === '烤烟');
          this.formData.leafTypeCode = leafTypeObj.leafTypeCode;
        }

        if (this.packTypeList && this.packTypeList?.length) {
          const packTypeObj = this.packTypeList.find((item) => item.name === '麻片');
          this.formData.packType = packTypeObj.enumKey;
        }
      }

      this.busiYear = data.busiYear;
      await this.handleSpec();

      console.log(this.formData);
    },

    // 处理申请码包数量
    handleApplyNum(type) {
      const { spec } = this.formData;
      const specValue = Number(spec) || 0;
      const { applyWeight } = this.formData;
      const applyWeightValue = Number(applyWeight * this.weightUnit) || 0;

      const applyNum = specValue === 0 ? 0 : (applyWeightValue / specValue).toFixed(2);
      this.formData.applyNum = Math.ceil(applyNum) || 0;

      if (type === 'applyWeight' && this.isEditor) {
        const { oldFormData } = this;
        oldFormData.applyWeight = (applyWeightValue * this.weightUnit).toFixed(2);
      }
    },
    // 根据包装类型获取包装规格数据源
    async handleSpec() {
      const { leafTypeCode } = this.formData;
      const { packType } = this.formData;
      if (leafTypeCode && packType) {
        this.formData.spec = undefined;
        const data = {
          applyOrgCd: this.formData.applyOrgCode,
          leafTypeCd: leafTypeCode,
          packType,
          // leafFormCd: '101',
          specState: '1',
        };
        const specList = await this.$apis.pcm.buy.packApply.querySpec(data);
        this.specList = specList?.rows || [];
      }
    },
    // 保存
    onSave() {
      this.$refs.modelRef.validate(async (valid) => {
        // debugger;
        if (!valid) return;
        this.saving = true;
        const data = this.formData;
        const { leafTypeCode } = data;
        const leafTypeObj = this.leafTypeList.find((item) => item.leafTypeCode === leafTypeCode);
        data.leafTypeName = leafTypeObj?.leafTypeName;

        // 保存的时候对申请量的单位进行转化
        if (this.isEditor) {
          const { oldFormData } = this;
          data.applyWeight = oldFormData.applyWeight;
        } else {
          data.applyWeight = (data.applyWeight * this.weightUnit).toFixed(2);
        }
        console.log(data);
        if (this.isEditor) {
          this.$apis.pcm.buy.packApply
            .update(data)
            .then((res) => {
              console.log(res);
              this.onclose();
              this.$emit('onQuery');
              this.$message.success('操作成功');
            })
            .catch((err) => {
              console.log(err);
              this.saving = false;
              // this.$message.error('操作失败');
            });
        } else {
          data.leafFormCode = '101';
          data.leafFormName = '原烟';
          this.$apis.pcm.buy.packApply
            .save(data)
            .then((res) => {
              console.log(res);
              this.onclose();
              this.$emit('onQuery');
              this.$message.success('操作成功');
            })
            .catch((err) => {
              console.log(err);
              // this.$message.error('操作失败');
              this.saving = false;
            });
        }
      });
    },
    // 取消
    onclose() {
      this.saving = false;
      // this.formData = {};
      this.specList = [];
      this.visible = false;
      this.$refs.modelRef.resetFields();
    },
  },
};
</script>
