<template>
  <ec-container height="100%">
    <ec-container-header>
      <ec-panel-item>
        <ec-panel-item-left>
          <ec-header title="收购码包申请" icon="onetomany" color-code="008" />
        </ec-panel-item-left>
      </ec-panel-item>
    </ec-container-header>
    <ec-container-body>
      <ec-panel :auto="false">
        <ec-panel-toolbar>
          <ec-v-item>
            <a-descriptions bordered :column="4">
              <a-descriptions-item label="调拨年度">
                {{ this.nowYear }}
              </a-descriptions-item>
              <a-descriptions-item label="烟叶类型"> {{ this.leafTypeName }}</a-descriptions-item>
              <a-descriptions-item label="计划量">
                {{ totalData.planTotalWeight }}
              </a-descriptions-item>

              <a-descriptions-item label="剩余计划量">
                {{ totalData.leftWeight }}
              </a-descriptions-item>
              <a-descriptions-item label="申请码包数量">
                {{ totalData.applyNum }}
              </a-descriptions-item>
              <a-descriptions-item label="申请量">
                {{ totalData.applyWeight }}
              </a-descriptions-item>
              <a-descriptions-item label=""> </a-descriptions-item>
              <a-descriptions-item label=""> </a-descriptions-item>
            </a-descriptions>
          </ec-v-item>
        </ec-panel-toolbar>
        <ec-panel-toolbar>
          <ec-v-item>
            <ec-query-panel :defaultFold="true" @query="$refs.table.pageAction(1)" @reset="onReset">
              <ec-query-item label="申请单位">
                <org-picker
                  :disabled="true"
                  v-model="queryItems.applyOrgCode"
                  placeholder="请选择"
                />
              </ec-query-item>

              <ec-query-item label="烟叶类型">
                <ec-select
                  showSearch
                  allowClear
                  placeholder="请选择"
                  v-model="queryItems.leafTypeCode"
                  @select="handleSpec"
                >
                  <a-select-option
                    v-for="leafTypeEk in leafTypeList"
                    :key="leafTypeEk.leafTypeCode"
                  >
                    {{ leafTypeEk.leafTypeName }}
                  </a-select-option>
                </ec-select>
              </ec-query-item>

              <ec-query-item label="包装类型">
                <ec-select placeholder="请选择" v-model="queryItems.packType" @select="handleSpec">
                  <a-select-option v-for="packType in packTypeList" :key="packType.enumKey">
                    {{ packType.name }}
                  </a-select-option>
                </ec-select>
              </ec-query-item>

              <template #inner>
                <ec-query-item label="规格">
                  <ec-select placeholder="请选择" v-model="queryItems.spec">
                    <a-select-option v-for="rtSpec in specList" :key="rtSpec.spec">
                      {{ rtSpec.spec }}
                    </a-select-option>
                  </ec-select>
                </ec-query-item>

                <ec-query-item label="状态">
                  <ec-select placeholder="请选择" v-model="queryItems.applyState">
                    <a-select-option v-for="applyState in applyStateList" :key="applyState.enumKey">
                      {{ applyState.name }}
                    </a-select-option>
                  </ec-select>
                </ec-query-item>
              </template>
            </ec-query-panel>
            <ec-divider />
          </ec-v-item>
        </ec-panel-toolbar>
        <ec-panel-header>
          <ec-v-item>
            <ec-panel-item>
              <ec-panel-item-left>
                <a-button type="primary" @click="onAdd">新增</a-button>
                <a-button @click="onDelete">删除</a-button>
                <a-button @click="onConfirm">确认</a-button>
                <!-- <a-button @click="onExport">导出</a-button> -->
              </ec-panel-item-left>
              <ec-panel-item-right>
                <a-space :size="15">
                  <span
                    >重量单位：<ec-select
                      showSearch
                      allowClear
                      placeholder="请选择"
                      v-model="weightUnit"
                      @select="handleUnitParse"
                      style="width: 150px"
                    >
                      <a-select-option
                        v-for="weightUnit in weightUnitList"
                        :key="weightUnit.weightUnitCd"
                      >
                        {{ weightUnit.weightUnitName }}
                      </a-select-option>
                    </ec-select></span
                  >
                </a-space>
              </ec-panel-item-right>
            </ec-panel-item>
          </ec-v-item>
        </ec-panel-header>

        <ec-panel-body height="100%">
          <ec-table
            ref="table"
            border
            height="100%"
            :auto="false"
            @page-action="handlePagination"
            :pagination="pagination"
            :data="tableData.rows"
            @checkbox-all="selectAllEvent"
            @checkbox-change="selectRowChange"
            :loading="loading"
            highlight-current-row
          >
            <ec-table-col type="checkbox" width="50" align="center"></ec-table-col>
            <ec-table-col title="序号" type="seq" width="70" align="center" />
            <ec-table-col field="applyDate" title="申请日期" align="center" width="200" />
            <ec-table-col field="applyOrgName" title="申请单位" align="left" min-width="150" />
            <ec-table-col field="leafTypeName" title="烟叶类型" align="left" min-width="150" />
            <ec-table-col field="packType" title="包装类型" align="left" min-width="150">
              <template #default="{ row }">
                <div v-for="packTypeObj in packTypeList" :key="packTypeObj.enumKey">
                  <span v-if="row.packType === packTypeObj.enumKey"> {{ packTypeObj.name }}</span>
                </div>
              </template>

              <!-- <a-select-option v-for="packType in packTypeList" :key="packType.enumKey">
                      {{ packType.name }}
                    </a-select-option> -->
            </ec-table-col>
            <ec-table-col field="spec" title="规格（KG/件）" align="right" min-width="150" />
            <ec-table-col field="applyNum" title="申请码包数量" align="right" min-width="150" />

            <ec-table-col field="applyWeight" title="申请量" align="right" min-width="150" />
            <ec-table-col field="applyState" title="状态" align="center" width="150">
              <template #default="{ row }">
                <div v-if="row.applyState === '1'">
                  <span>已确认</span>
                </div>
                <div v-else>
                  <span>未确认</span>
                </div>
              </template>
            </ec-table-col>

            <ec-table-col title="操作" align="center" width="150" fixed="right">
              <template #default="{ row }">
                <a-button type="link" @click="onEdit(row)">编辑</a-button>
              </template>
            </ec-table-col>
          </ec-table>
        </ec-panel-body>
      </ec-panel>

      <buy-apply-edit @onQuery="onQuery" ref="BuyApplyEdit"></buy-apply-edit>
    </ec-container-body>
  </ec-container>
</template>

<script>
import BuyApplyEdit from './BuyApplyEdit.vue';
import { OrgPicker } from '@euler-material/ecm-ui';
import _ from 'lodash';

export default {
  name: 'ExecutionManage',
  components: {
    BuyApplyEdit,
    OrgPicker,
  },
  data() {
    return {
      objectData: {},
      busiYear: '',
      queryItems: {
        applyOrgCode: undefined,
        leafTypeCode: '',
        packType: '',
        spec: '',
        applyState: '',
      },
      defaultPackType: '',
      weightUnit: 1,
      areaUnit: 10000,
      nowYear: '',
      loading: false,
      leafTypeCd: '',
      leafTypeList: [],
      oldLeafTypeList: [],
      oldDataRow: [], // 拷贝的旧数据，单位切换的时候要在旧的数据上转换
      oldTotalData: [], // 拷贝的旧上级数据，单位切换的时候要在旧的数据上转换
      totalData: {}, // 合计的数据
      isNullData: [],
      leafTypeName: '',
      bizCode: '',
      beforeLeafTypeName: '',

      weightUnitList: [
        { weightUnitCd: 1, weightUnitName: '公斤', isSelected: false },
        { weightUnitCd: 50, weightUnitName: '担', isSelected: false },
        // { weightUnitCd: 500000, weightUnitName: '万担', isSelected: false },
      ],

      applyStateList: [],
      packTypeList: [],
      specList: [],
      enumMap: {},
      pagination: {
        pageNumber: 1,
        pageSize: 10,
        total: 0,
      },
      tableData: {
        // 数据表格
        // totalRows: 0,
        rows: [],
      },
    };
  },
  computed: {
    appConfig() {
      return this.$store.getters.getAppConfig;
    },
  },
  async created() {
    const { appCode } = this.$router.history.current.query;
    this.appCode = appCode || 'CM'; // 获取当前登入信息

    const enumTypeCodes = ['PC_PACK_TYPE', 'PC_APPLY_STATE', 'PC_RT_SPEC'];
    const data = {
      enumTypeCodes,
    };
    const res = await this.$apis.enum.index.getEnumType(data);
    const enumMap = res?.enumMap || {};
    this.enumMap = _.cloneDeep(enumMap);
    const applyStateList = enumMap?.PC_APPLY_STATE || [];
    if (applyStateList && applyStateList.length > 0) {
      applyStateList.unshift({ enumKey: '', enumTypeCode: 'PC_APPLY_STATE', name: '全部' });
      this.queryItems.applyState = applyStateList[0].enumKey;
    }
    this.applyStateList = applyStateList;
    const packTypeList = enumMap?.PC_PACK_TYPE || [];
    if (packTypeList && packTypeList.length > 0) {
      packTypeList.unshift({ enumKey: '', enumTypeCode: 'PC_PACK_TYPE', name: '全部' });
      packTypeList.some((item) => {
        if (item.name === '麻片') {
          this.queryItems.packType = item.enumKey;
          this.defaultPackType = item.enumKey;
          return true;
        } else {
          return false;
        }
      });
    }
    this.packTypeList = packTypeList;

    console.log(res);
  },
  methods: {
    async handleSpec() {
      const data = {
        applyOrgCd: this.queryItems.applyOrgCd,
        leafTypeCd: this.queryItems.leafTypeCode,
        packType: this.queryItems.packType,
        // leafFormCd: '101',
        specState: '1',
      };
      console.log(`包装规格参数：${JSON.stringify(data)}`);
      const specList = await this.$apis.pcm.buy.packApply.querySpec(data);
      this.specList = specList?.rows || [];
      console.log(`包装规格：${JSON.stringify(specList)}`);
      this.beforeLeafTypeName = this.handleLeafTypeName(this.queryItems.leafTypeCode);
    },
    // 处理单位转换
    handleUnitParse() {
      //  列表的单位转换
      const handleOlDataRow = JSON.parse(JSON.stringify(this.oldDataRow));
      handleOlDataRow.forEach((item) => {
        const itemObj = item;
        itemObj.applyWeight = (item.applyWeight / this.weightUnit).toFixed(2);
      });

      const handleOldTotalData = JSON.parse(JSON.stringify(this.oldTotalData));

      // 汇总数据
      handleOldTotalData.planTotalWeight = (
        handleOldTotalData.planTotalWeight / this.weightUnit
      ).toFixed(2);
      handleOldTotalData.leftWeight = (handleOldTotalData.leftWeight / this.weightUnit).toFixed(2);
      handleOldTotalData.applyWeight = (handleOldTotalData.applyWeight / this.weightUnit).toFixed(
        2,
      );

      this.totalData = handleOldTotalData;
      this.tableData.rows = handleOlDataRow;
    },

    async handlePagination(pageNumber, pageSize) {
      this.pagination.pageNumber = pageNumber;
      this.pagination.pageSize = pageSize;
      await this.handleGetLeafTypeList();
    },

    // 获取烟叶类型数据
    async handleGetLeafTypeList() {
      if (!this.leafTypeList?.length) {
        const res = await this.$apis.common.index.queryLeafType({});
        const rows = res?.rows || [];
        this.oldLeafTypeList = _.cloneDeep(rows);
        if (rows && rows.length > 0) {
          rows.unshift({ leafTypeName: '全部', leafTypeCode: '' });
        }
        this.leafTypeList = rows;
        this.leafTypeList.some((item) => {
          if (item.leafTypeName === '烤烟') {
            this.leafTypeCd = item.leafTypeCode;
            this.queryItems.leafTypeCode = item.leafTypeCode;
            this.leafTypeName = '烤烟';
            return true;
          } else {
            return false;
          }
        });
      }
      if (!this.queryItems.leafTypeCode) {
        const { leafTypeCode } = this.leafTypeList[0];
        this.leafTypeCd = leafTypeCode;
        this.queryItems.leafTypeCode = leafTypeCode;
        this.leafTypeName = this.leafTypeList[0].leafTypeName;
      }
      if (!this.orgCode) {
        const orgObj = await this.$apis.bim.curOrg.get({});
        console.log(`当前用户信息：${JSON.stringify(orgObj)}`);
        this.bizCode = orgObj?.bizCode;
        this.orgCode = orgObj?.orgUniqueCode; // todo 写死数据
        this.orgName = orgObj?.orgName;

        this.queryItems.applyOrgCode = this.bizCode;
      }
      if (!this.queryItems.busiYear > 0) {
        // 获取年度
        const data = {
          bizCode: this.appConfig.bizCode, // todo 写死的数据
          appCode: this.appCode || 'CM',
          yearTypeCode: 'SM_YEAR',
        };
        const busiYearObj = await this.$apis.bpm.executionEnter.getBuYear(data);
        this.queryItems.busiYear = busiYearObj?.yearValue;
        this.nowYear = busiYearObj?.yearValue;
      }
      this.onQuery();
    },

    // 数据初始化
    async onQuery() {
      this.loading = true;
      this.leafTypeName = this.beforeLeafTypeName || this.leafTypeName;

      const data = {
        pageNo: this.pagination.pageNumber,
        pageSize: this.pagination.pageSize,
        leafTypeCode: this.queryItems.leafTypeCode,
        packType: this.queryItems.packType,
        spec: this.queryItems.spec,
        applyState: this.queryItems.applyState,
        applyOrgCode: this.queryItems.applyOrgCode,
        tranYear: this.nowYear,
        applyType: 'BU_CP', // 写死
        leafFormCode: '101',
      };
      console.log(JSON.stringify(data));
      await this.$apis.pcm.buy.packApply
        .query(data)
        .then((res) => {
          const rows = res.rows || [];
          this.pagination.total = res.totalRows;
          this.oldDataRow = JSON.parse(JSON.stringify(rows));
          // this.tableData.rows = rows;
        })
        .catch((err) => {
          console.log(err);
        })
        .finally(() => {
          this.loading = false;
          this.selectedRows = [];
        });

      const totalData = await this.$apis.pcm.buy.packApply.queryTotalData(data);
      this.oldTotalData = JSON.parse(JSON.stringify(totalData));
      this.handleUnitParse();
    },

    // 同意
    onConfirm() {
      if (!this.selectedRows || this.selectedRows.length <= 0) {
        this.$message.error('请选择一条数据再同意');
        return;
      }
      const submitData = this.selectedRows.find((item) => item.applyState === '1');
      console.log(`--${JSON.stringify(submitData)}`);
      if (submitData) {
        this.$message.error('存在已确认数据，不能重复确认！');
        return;
      }
      this.$confirm({
        title: '是否执行该操作?',
        content: () => (
          <div style="color:red;">
            是否确认提交选中的数据，确认请点击'确认'按钮，取消点击'取消'按钮。
          </div>
        ),
        onOk: () => {
          this.loading = true;
          const idList = [];
          for (let i = 0; i < this.selectedRows.length; i++) {
            idList.push(this.selectedRows[i].barcodeApplyId);
          }
          console.log(`ids:${JSON.stringify(idList)}`);
          this.$apis.pcm.buy.packApply
            .confirm({ barcodeApplyIds: idList })
            .then((res) => {
              console.log(JSON.stringify(res));
              if (res.result) {
                this.loading = false;
                this.$message.success('操作成功');
                this.onQuery();
              } else {
                this.loading = false;
                this.$message.error('操作失败');
              }
            })
            .catch((err) => {
              console.log(err);
              this.loading = false;
              // this.$message.error('操作失败');
            });
        },
      });
    },
    // 删除
    onDelete() {
      if (!this.selectedRows || this.selectedRows.length <= 0) {
        this.$message.error('请选择一条数据再同意');
        return;
      }
      const submitData = this.selectedRows.find((item) => item.applyState === '1');
      console.log(`--${JSON.stringify(submitData)}`);
      if (submitData) {
        this.$message.error('存在已确认数据，不能删除！');
        return;
      }
      this.$confirm({
        title: '是否执行该操作?',
        content: () => (
          <div style="color:red;">
            是否确认提交选中的数据，确认请点击'确认'按钮，取消点击'取消'按钮。
          </div>
        ),
        onOk: () => {
          this.loading = true;
          const idList = [];
          for (let i = 0; i < this.selectedRows.length; i++) {
            idList.push(this.selectedRows[i].barcodeApplyId);
          }
          console.log(`ids:${JSON.stringify(idList)}`);
          this.$apis.pcm.buy.packApply
            .delete({ barcodeApplyIds: idList })
            .then((res) => {
              console.log(JSON.stringify(res));
              if (res.result) {
                this.loading = false;
                this.$message.success('操作成功');
                this.onQuery();
              } else {
                this.loading = false;
                this.$message.error('操作失败');
              }
            })
            .catch((err) => {
              console.log(err);
              this.loading = false;
              // this.$message.error('操作失败');
            });
        },
      });
    },

    // 新增
    onAdd() {
      const { busiYear } = this.queryItems;
      const weightUnitObj = this.weightUnitList.find((res) => res.weightUnitCd === this.weightUnit);
      const data = {
        busiYear,
        enumMap: this.enumMap,
        oldLeafTypeList: this.oldLeafTypeList,
        orgCodeList: this.queryItems.applyOrgCode,
        orgCode: this.orgCode,
        orgName: this.orgName,
        weightUnitObj,
      };
      this.$refs.BuyApplyEdit.show(data);
    },

    // 编辑
    onEdit(row) {
      if (row.applyState === '1') {
        this.$message.error('已确认数据不能编辑！');
        return;
      }

      const { busiYear } = this.queryItems;

      const selectRow = this.oldDataRow.find((item) => item.barcodeApplyId === row.barcodeApplyId);
      // const selectedRow = JSON.parse(JSON.stringify(row)); // 深拷贝
      const weightUnitObj = this.weightUnitList.find((res) => res.weightUnitCd === this.weightUnit);
      const data = {
        selectedRow: _.cloneDeep(selectRow),
        busiYear,
        enumMap: this.enumMap,
        oldLeafTypeList: this.oldLeafTypeList,
        orgCodeList: this.queryItems.applyOrgCode,
        weightUnitObj,
        type: 'editor',
      };
      console.log(`0000000:${JSON.stringify(data)}`);
      this.$refs.BuyApplyEdit.show(data);
    },

    // 重置
    onReset() {
      this.queryItems.leafTypeCode = this.leafTypeCd;
      this.queryItems.packType = this.defaultPackType;
      this.queryItems.spec = '';
      this.queryItems.applyState = '';
      this.beforeLeafTypeName = this.handleLeafTypeName(this.leafTypeCd);
      this.$refs.table.pageAction(1);
    },

    // 根据编码翻译对应的名称
    handleLeafTypeName(typeCd) {
      const leafType = this.leafTypeList.find((item) => item.leafTypeCode === typeCd);
      return leafType?.leafTypeName;
    },

    // 选中所有列表
    selectAllEvent({ checked, records }) {
      console.log(`${checked}-选中所有records包含选中的所以对象----${JSON.stringify(records)}`);
      this.selectedRows = records;
    },

    // 选中列表
    selectRowChange({ checked, records }) {
      console.log(
        `${checked}-选择改变时会把records包含选中的所以对象----${JSON.stringify(records)}`,
      );
      this.selectedRows = records;
    },
  },
};
</script>
<style lang="less" scoped>
.year {
  font-size: 18px;
  line-height: 22px;

  color: #18191a;
  font-weight: 700;
  padding-top: 18px;
  padding-right: 50px;
  font-family: '微软雅黑 Bold', '微软雅黑', 'Microsoft YaHei', sans-serif;
}
.paddingTop {
  padding-top: 15px;
}
</style>
